.icon-phone:before {
  content: ""; }

.icon-gmail:before {
  content: ""; }

.icon-telegram:before {
  content: ""; }

.icon-vk:before {
  content: ""; }

[class^="icon-"] {
  margin: 0em .1em;
  padding: .3em .3em;
  display: inline-block;
  vertical-align: middle; }
  [class^="icon-"]:before {
    font-family: icomoon;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }

.icon-vk {
  color: #4C75A3; }

.icon-gmail {
  color: #E04C40; }

.icon-telegram {
  color: #2CA5E0; }

@font-face {
  font-family: "icomoon";
  src: url("../../assets/fonts/icomoon/icomoon.eot?") format("eot"), url("../../assets/fonts/icomoon/icomoon.ttf") format("truetype"), url("../../assets/fonts/icomoon/icomoon.woff") format("woff"); }

/* layout */
p {
  margin-top: 0;
  margin-bottom: 15px; }

.flexbox {
  display: flex; }

.flex-items-center {
  align-items: center; }

.cv__container {
  margin-left: auto;
  margin-right: auto;
  max-width: 940px;
  font-family: "Roboto", sans-serif; }
  .cv__container * {
    box-sizing: border-box; }

.cv__content-block {
  margin-left: 15px;
  margin-right: 15px;
  overflow: hidden;
  padding-top: 20px;
  padding-bottom: 20px; }

.cv__row-wrapper {
  margin-right: -15px;
  margin-left: -15px;
  padding-bottom: 10px;
  display: flex; }
  .cv__row-wrapper.cv-section-title__container {
    align-items: center; }
  .cv__row-wrapper:not(.cv-section-title__container) {
    margin-bottom: 15px; }
  .cv__row-wrapper:not(.cv-section-title__container):not(:last-child) {
    border-bottom: 1px dashed rgba(0, 0, 0, 0.24); }
  @media (max-width: 640px) {
    .cv__row-wrapper {
      flex-direction: column; }
      .cv__row-wrapper:not(.cv-section-title__container) {
        display: block; }
        .cv__row-wrapper:not(.cv-section-title__container) .cv-col-title {
          margin-bottom: 10px; }
      .cv__row-wrapper.cv-section-title__container {
        justify-content: center; }
        .cv__row-wrapper.cv-section-title__container .cv__col {
          min-width: 0;
          flex: none; } }

.cv__col {
  padding-right: 15px;
  padding-left: 15px; }

.cv-col-title {
  position: relative;
  flex: 2;
  min-width: 220px;
  font-weight: 600;
  font-size: 15px;
  line-height: 19px; }

.cv-col-content {
  position: relative;
  flex: 5;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px; }

.cv-section-title {
  margin-right: 30px;
  font-size: 28px;
  font-weight: 300;
  line-height: 30px;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-align: center;
  white-space: nowrap;
  color: #099778; }
  @media (max-width: 640px) {
    .cv-section-title {
      position: relative;
      display: inline-block;
      margin-right: 0;
      white-space: normal; }
      .cv-section-title:after, .cv-section-title:before {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        height: 1px;
        width: 9999px;
        background-color: rgba(6, 103, 82, 0.2); }
      .cv-section-title:before {
        right: 100%;
        margin-right: 15px; }
      .cv-section-title:after {
        left: 100%;
        margin-left: 15px; } }

.cv__delimiter {
  height: 1px;
  width: 100%;
  background-color: rgba(6, 103, 82, 0.2); }
  @media (max-width: 640px) {
    .cv__delimiter {
      display: none; } }

/* header section */
.cv-section-header__title-about {
  font-size: 20px; }
  .cv-section-header__title-about .position {
    float: left; }
  .cv-section-header__title-about .form-type {
    float: right; }

.cv-section-header__author-name {
  margin-bottom: 10px;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.8); }

.cv_position {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.8); }

.cv-section-header__contact-info {
  margin-top: 50px; }

.cv-section-header__author-photo {
  border-radius: 1000px;
  margin-left: auto;
  margin-right: auto;
  width: 200px;
  height: 200px;
  background: url("../../assets/img/avatar.jpg") no-repeat;
  background-size: 300px;
  background-position: -68px center; }

.cv-contact__socials-list {
  padding-left: 0;
  font-weight: 500; }

.cv-contact__socials-item {
  list-style: none;
  margin-bottom: 5px; }
  .cv-contact__socials-item [class^="icon-"] {
    margin-right: 5px; }
    .cv-contact__socials-item [class^="icon-"]:before {
      font-size: 1.4em; }
  .cv-contact__socials-item a {
    color: rgba(0, 0, 0, 0.71); }

/* technical skills */
.cv__skills-list {
  padding-left: 10px;
  margin: 0; }

.cv-skill {
  list-style: none;
  padding: 10px 0; }
  .cv-skill:before {
    content: '—';
    margin-right: 10px;
    float: left; }

/* portfolio */
.cv__portfolio-container {
  justify-content: center;
  align-items: center; }
  @media (max-width: 875px) {
    .cv__portfolio-container {
      flex-direction: column; } }

.cv-portfolio-item__slides-list {
  padding-left: 0;
  height: 360px; }

.cv-portfolio-item__slides-item img {
  max-width: 100%; }

.cv-portfolio__page-list {
  padding-left: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around; }
  @media (max-width: 875px) {
    .cv-portfolio__page-list {
      flex-direction: row;
      justify-content: flex-start; } }

.cv-portfolio__page-item {
  margin-bottom: 5px;
  padding-top: 3px;
  padding-bottom: 3px;
  list-style: none; }
  .cv-portfolio__page-item.active a {
    color: #066752; }
  .cv-portfolio__page-item.active .cv__bullet:after {
    opacity: 1;
    transform: scale(1);
    background-color: rgba(152, 111, 0, 0.88); }
  .cv-portfolio__page-item a {
    text-decoration: none;
    color: #000;
    font-weight: 500; }
  .cv-portfolio__page-item .cv__bullet {
    position: relative;
    border-radius: 1000px;
    border: 3px solid #099778;
    margin-right: 10px;
    height: 20px;
    width: 20px;
    display: inline-block;
    vertical-align: middle; }
    .cv-portfolio__page-item .cv__bullet:after {
      border-radius: 1000px;
      position: absolute;
      top: 4px;
      left: 4px;
      width: 7px;
      height: 7px;
      opacity: 0;
      transform: scale(0);
      transition: all .3s ease-in-out;
      background-color: #099778;
      content: ''; }
  @media (max-width: 875px) {
    .cv-portfolio__page-item:not(:last-child) {
      margin-right: 20px; } }
